.search-form {
  /* we'll re-use this number, so DRYing up */
  --searchButtonWidth: 75px;

  overflow: hidden;
  position: relative;
}



.search-input {
  /* take full width of form */
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}
.buttonStyle {
  background-color: white;
  color: black;
  border: 2px solid #4caf50;
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
}
.submit-button {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 10px;
}
